.web .project-container {
    margin-top: -2rem;
}
.web .project-container a {
    background-color: rgb(0, 0, 0);
    width: 250px;
    z-index: 20;
    padding: 0.8rem;
    display: -ms-flexbox;
    display: flex;
    justify-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0.5rem auto;
    cursor: pointer;
    -webkit-transition: -webkit-transform .15s ease-in-out;
    transition: -webkit-transform .15s ease-in-out;
    -o-transition: transform .15s ease-in-out;
    transition: transform .15s ease-in-out;
    transition: transform .15s ease-in-out, -webkit-transform .15s ease-in-out;
    -webkit-transition: transform .20s ease-in-out;
    position: relative;
    z-index: 1;
    border-radius: 8px;
}

.web .project-container img {
    position: relative;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 5px;
    padding: 0.7rem 0.3rem 0.7rem 0.3rem;
    background-color: rgb(34, 34, 34);
    border: 1px solid rgba(255, 255, 255, 0.322);
    margin-bottom: 0.3rem;
    width: 130px;
    height: 250px;
    -ms-flex-item-align: center;
        align-self: center;
}

.web .project-container h2 {
    color: rgb(255, 154, 46);
}

.web .project-container h3 {
    font-size: .9rem;
    padding-bottom: 0.8rem;
    color: rgb(219, 219, 219);
}

.web .project-container .tags p {
    background: #6A9113;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #1f0000, rgb(0, 0, 0));
    background: -webkit-gradient(linear, left top, right top, from(#1f0000), to(rgb(0, 0, 0)));
    background: -webkit-linear-gradient(left, #1f0000, rgb(0, 0, 0));
    background: -o-linear-gradient(left, #1f0000, rgb(0, 0, 0));
    background: linear-gradient(to right, #1f0000, rgb(0, 0, 0));
    border: 1px solid rgba(255, 255, 255, 0.281);
    font-size: .7rem;
    border-radius: 8px;
    text-align: center;
    display: inline-block;
    padding: 0.2rem;
    margin: 0.2rem;
    color: white;
    line-height: 90%;
}

.web .project-container:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.description {
    position: relative;
    left: 220px;
    top: 40px;
    text-align: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: black;
    margin: 0;
    z-index: 5;
    font-size: 3rem;
    color: rgba(255, 154, 46, 0.384);
    line-height: 105%;
    cursor: pointer;
}

.overlay-text {
    font-size: 15px;
    background-color: rgb(255, 154, 46);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.overlay-text p {
    color: black;
    width: 80%;
}


