.page2 {
    background-color: rgb(34, 34, 34);
    padding: 2rem;
    z-index: 0;
    height: auto;
    color: white;
}
.page2:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.1;
    background-image: url('images/editor.jpg');
    background-repeat: no-repeat;
    background-position: 50% 0;
    -ms-background-size: cover;
    background-size: cover;
    z-index: -1;
}
.dev-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    height: 80px;
    margin-bottom:1rem;
}
.page2 h1 {
    /*font-family: 'Courier New', Courier, regular, monospace;*/
    font-weight: normal;
    color: yellow;
    width: 30%;
}
.dev-github {
    border-radius: 8px;
    color: white;
    background-color: black;
    padding: 0.5rem;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: center;
        align-items: center;
    height: 70px;
}
.dev-github h3 {
    text-align: right;
    font-weight: normal;
}
.dev-social {
    background-color: white;
    margin-left: 0.5rem;
    width: 3.4rem;
    height: 3.4rem;
    border-radius: 50%;
    font-size: 2.24rem;
    text-align: center;
    -webkit-transition: -webkit-transform .15s ease-in-out;
    transition: -webkit-transform .15s ease-in-out;
    -o-transition: transform .15s ease-in-out;
    transition: transform .15s ease-in-out;
    transition: transform .15s ease-in-out, -webkit-transform .15s ease-in-out;
    -webkit-transition: transform .20s ease-in-out;
}
.dev-social:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}
.dev-social .svg-inline--fa {
    vertical-align: -0.8rem;
    color: black;
}
.dev-social a {
    cursor:pointer;
}
.web {
    z-index: 100;
    height: 75%;
    width: 95%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}


